.section-about {
  background-color: $color-grey-light-1;
  padding: 25rem 0;
  margin-top: -20vh;
}

.section-features {
  padding: 20rem 0;
  background-image: linear-gradient(
    to right bottom,
    rgba($color-primary-light, 0.8),
    rgba($color-primary-dark, 0.8)), 
    url(../img/nat-4.jpg);
  background-size: cover;
  margin-top: -10rem;

  transform: skewY(-7deg); 

  & > * {
    // direct child selector
    transform: skewY(7deg);
  }

}

.section-tours {
  background-color: $color-grey-light-1;
  padding: 25rem 0 15rem 0;
  margin-top: -10rem;
}

.section-stories {
  position: relative;
  padding: 15rem 0;
  //background-color: $color-grey-dark;
}

.section-book {
  padding: 15rem 0;
  background-image: linear-gradient(to right bottom, $color-primary-light, $color-primary-dark);
}

.book {
  background-image: linear-gradient(105deg, rgba($color-white, .9) 50%, transparent 50%), url(../img/nat-10.jpg); // make / with linear gradient solid-color gradient
  background-size: cover; // use can use 100%  also
  border-radius: 3px;
  box-shadow: 0 1.5rem 4rem rgba($color-black, .15);

  height: 50rem;


  &__form {
    width: 50%;
    padding: 6rem;
  }

}
