.navigation {
  //display: none;

  &__checkbox{
    display: none;
  }

  &__button {
    background-color: $color-white;
    height: 7rem; // height 7, width 7 to cover the __background in the begining
    width: 7rem;
    position: fixed;
    top: 6rem;
    right: 6rem;
    border-radius: 50%;
    z-index: 2000;
    box-shadow: 0 1rem 3rem rgba($color-black, .1);
    cursor: pointer;
    text-align: center;
  }

  &__background {
    // green circle behind hamburger 
    height: 6rem;
    width: 6rem;
    border-radius: 50%;
    position: fixed;
    top: 6.5rem;
    right: 6.5rem;
    background-image: radial-gradient($color-primary-light, $color-primary-dark);
    z-index: 1000; //this is only work with element which position defined
  }

  &__nav {
    height: 100vh;
    width: 100%; // you don't have to give this property be cause
    // div is block element which is occupy whole width;
    position: fixed;
    top: 0;
    left: -100%;
    z-index: 1500;

    opacity: 0;
    width: 0;
    transition: all .8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }

  &__list {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    list-style: none;
    text-align: center;
  }

  &__item {
    margin: 1rem;
  }

  &__link {
    &:link,
    &:visited {
      display: inline-block;
      font-size: 3rem;
      font-weight: 300;
      padding: 1rem 2rem;
      color: $color-white;
      text-decoration: none;
      text-transform: uppercase;
      background-image: linear-gradient(120deg, transparent 50%, $color-white 50%);
      background-size: 250%;
      transition: all .4s;

      span {
        margin-right: 1.5rem;
        display: inline-block;
      }
    }

    &:hover,
    &:active {
      background-position: 100%;
      color: $color-primary;
      transform: translateX(1rem); //if not inline-block this won't work
    }
  }

  &__checkbox:checked ~ &__background {
    transform: scale(80);
  }

  &__checkbox:checked ~ &__nav {
    opacity: 1;
    width: 100%;
    left: 0;
  }

  // ICON
  &__icon {
    position: relative;
    margin-top: 3.5rem;

    &,
    &::before,
    &::after {
      width: 3rem;
      height: 2px;
      background-color: $color-grey-dark-3;
      display: inline-block;
    }

    &::before,
    &::after {
      content:"";
      position: absolute;
      left: 0;
      transition: all .2s;
    }

    &::before {top: -.8rem;}
    &::after {top: .8rem;}

  }

  &__button:hover &__icon::before {top: -1rem;}
  &__button:hover &__icon::after {top: 1rem;}


  &__checkbox:checked + &__button &__icon { //select sibling and then child
    background-color: transparent; //hide middle dash
  }

  &__checkbox:checked + &__button &__icon::before { //select sibling and then child
    top: 0; //put dash back to middle
    transform: rotate(135deg);
  }

  &__checkbox:checked + &__button &__icon::after { //select sibling and then child
    top: 0;
    transform: rotate(-135deg);
  }
}
